@import '../../Style/GeneralStyle.scss';

.skill{
    background-color: $SystemDarkColor;
    color: $SystemWhiteColor;
    padding: 7px 10px;
    border-radius: 10px;
    @include RegularParagraph2;
    transition: 0.5s;
}
@media (max-width: $LaptopWidth){
    .skill{
        box-shadow: 2px 3px 5px 1px $SystemBlue3Color;
    }
}
@media (max-width: $BigMobile){
    .skill{
        padding: 2px 5px;
        border-radius: 6px;
        font-size: 14px;
    }
}

@media (max-width: $Mobile){
    .skill{
        padding: 1px 2px;
    }
}